import React, { useContext } from 'react';
import { UserContext } from '../Contexts/UserContexts';

import creatorIMG from '../img/lotta.jpg';
import kokeilu1 from '../img/kokeilu1.jpg';
import kokeilu2 from '../img/kokeilu2.jpg';
import arvio from '../img/arvio.jpg';

export const AboutContent = () => {
    return (
        <div className='About'>
            <h2>Tietoja projektista</h2>
            <div className='section'>
                <h3>Tekij</h3>
                <div className='personContainer'>
                    <img id='face' alt='Kuva sivuston tekijst' src={creatorIMG}/>
                    <p>
                        Lotta Laukkanen, kolmannen vuoden tieto- ja viestinttekniikan opiskelija Metropolia Ammattikorkeakoulussa.
                    </p>
                </div>
            </div>
            <div className='section'>
                <h3>Sovelluksen yleiskuvaus</h3>
                <p>
                    Decamerone on kirjoittamisesta kiinnostuneille suunnattu alusta, jossa kyttjt voivat 
                    jakaa ja tarkastella muiden tuottamia lyhyit tekstej eli raapaleita tai novelleja. 
                    Kyttji yhdist kiinnostus kirjoittamiseen erityisesti tekstin tuottamisen nkokulmasta, 
                    mutta palvelulla on tarjottavaa mys lhinn lyhytproosan lukemisesta kiinnostuneille.
                </p>
            </div>
            <div className='section'>
                <h3>Kokeilut ja arviot</h3>
                <h4>Ensimminen kokeilu: konsepti</h4>
                <img id='kokeilu1' alt='Kuva ensimmisen kokeilun kokeilusuunnitelmasta' src={kokeilu1}/>
                <p>
                    Ennen palvelun kehitystyn kynnistmist tehtiin pieni selvitys Facebookin kautta. Tavoitteena 
                    oli selvitt olisiko tllaiselle palvelulle kysynt. Kyselyyn osallistujat olivat raapaleiden 
                    jakamiseen keskittyvn ryhmn jseni. Tulokset olivat lupaavia, seitsemst vastaajasta kaikki 
                    vastasivat 'kyll' tai 'ehk' kysymykseen kiinnostuksesta rekisterity kyttjksi tllaiseen 
                    verkkopalveluun.
                </p>
                <img id='kokeilu2' alt='Taulukko ensimmisen kokeilun tuloksista' src={kokeilu2}/>
                <h4>Toinen kokeilu, korvattiin heuristisella arvioinnilla</h4>
                <p>
                    Heuristisessa arvioinnissa sovelluksen prototyyppi kytiin lpi pyrkien huomaamaan mahdollisimman 
                    moni kytettvyyteen ja kyttjkokemukseen vaikuttavista epkohdista. Suurin osa huomatuista 
                    johtui toki prototyypin karkeudesta, mutta joitakin hydyllisikin havaintoja arvioon sisltyi.
                </p>
                <img id='arvio' alt='Taulukko ensimmisen kokeilun tuloksista' src={arvio}/>
            </div>
            <div className='section'>
                <h3>Oppimiskokemuksena</h3>
                <p>
                    Projekti on jlleen laajentanut React-osaamista: erityisesti Context API on tullut tutuksi. 
                    Kyttjtutkimuksen osalta heuristisen arvioinnin tekeminen oli uutta, vaikka konsepti olikin 
                    viime kevlt tuttu.
                </p>
                <p>
                    Jos tekisin projektin uudelleen, ajoittaisin sen niin, ett kyttjtestej 
                    voisi tehd kyttjien kanssa, sill niss oloissa kytettvyystestaus ji parhaassakin 
                    tapauksessa mututuntuman varaan. Jostain syyst rakensin mys osan komponenteista alun perin 
                    todella kummalliseen muotoon, mik kostautui viimeistn ulkoasua silotellessa. Wrapper divej 
                    ei pitisi vierastaa nin kovasti.
                </p>
                <p>
                    Onnistuneimmaksi tss kokonaisuudessa koen oman riman asettamisen: olisin varmasti saanut 
                    palveluun lisominaisuuksia ja parannettua nykyisikin, mutta ptin etten aio menett yuniani 
                    suorittaessani kahta opintokokonaisuutta kerralla. Tm tavoite toteutui niin, ett tmn 
                    sooloprojektin lopputuote on jopa varsin kelvollinen.
                </p>
            </div>
            <div id='signature'>~ Lotta Laukkanen ~</div>
        </div>
    );
}